.body {
    position: relative;
    width: 100%;
    height: 100%;
    /* background-color: #fff; */
    /* overflow: hidden; */
    display: flex;
    justify-content: center;
    align-items: center;
    transition: background-color .1s;
    margin-bottom: 24px;
  }
  
  .content {
    display: flex;
    flex-direction: column;
    margin: auto;
    align-items: center;
    width: 100%;
    transform: translateY(5%);
  }
  
  label, .toggle {
    height: 2.8rem;
    border-radius: 100px;
  }
  label {
    width: 100%;
    background-color: rgba(0,0,0,.1);
    border-radius: 100px;
    position: relative;
    cursor: pointer;
    padding: 1%;
  }
  .toggle {
    position: absolute;
    width: 49%;
    background-color: #28292E;
    
    box-shadow: 0 2px 15px rgba(0,0,0,.15);
    transition: transform .3s cubic-bezier(0.25, 0.46, 0.45, 0.94);
  }
  .names {
    font-size: 90%;
    font-weight: bolder;
    width: 65%;
    margin-top: 2.5%;
    position: absolute;
    display: flex;
    justify-content: space-between;
    user-select: none;
    
  }
  
  [type="checkbox"] + .app .dark {
      margin-right: -22%;
      /* color: #000; */
      display: flex;
      justify-content: center;
      align-items: center;
  }
  
  [type="checkbox"] + .app .light {
      margin-left: 26%;
      /* color: #fff; */
      display: flex;
      justify-content: center;
      align-items: center;
  }

  [type="checkbox"] + .app .light > div:last-child {
    color: #fff;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
  }

  [type="checkbox"] + .app .light > div:first-child {
    margin-right: 10px;
  }

  [type="checkbox"] + .app .dark > div:last-child {
    color: #000;
    font-style: normal;
    font-weight: 400;
    font-size: 20px;
    line-height: 120%;
  }

  [type="checkbox"] + .app .dark > div:first-child {
    margin-right: 10px;
  }
  
  [type="checkbox"] {
    display: none;
  }
  [type="checkbox"]:checked + .app .toggle{
    transform: translateX(100%);
    background-color: #28292E;
  }
  [type="checkbox"]:checked + .app .dark > div:last-child{
    color: #fff;
  }
  [type="checkbox"]:checked + .app .light > div:last-child{
    color: #000;
  }

  [type="checkbox"]:checked + .app .dark > div:first-child{
    filter: invert(1);
  }

  [type="checkbox"]:checked + .app .light > div:first-child{
    filter: invert(1);
  }
  
