#FuseNft {
  padding: 0 130px;
  margin-bottom: 254px;
  text-align: center;
}

#FuseNft .heading {
  color: var(--dark-blue);
  margin-bottom: 16px;
}

.fuse-nft-image {
  height: 300px;
  width: 300px;
}

#FuseNft .sub-heading {
  margin-bottom: 136px;
}

#FuseNft .fusenft-imgs-container {
  display: flex;
  justify-content: space-between;
}

.knowmore {
  font-family: var(--secondary-font);
  font-style: normal;
  font-weight: 500;
  font-size: 24px;
  line-height: 170%;
  text-decoration-line: none;
  color: #1d97de;
  cursor: pointer;
}

@media screen and (max-width: 768px) {
  #FuseNft {
    padding: 16px;
    margin-bottom: 128px;
  }

  #FuseNft .heading {
    line-height: 120%;
    margin-bottom: 32px;
  }

  #FuseNft .sub-heading {
    margin-bottom: 32px;
  }

  .knowmore {
    font-size: 16px;
  }
}
