#Home {
  background: url("../../../images/background/homapage-light.png") center center;
  background-size: cover;
  overflow: hidden;
  max-width: 100vw;
}

.divider {
  display: flex;
  justify-content: center;
  align-items: center;
}

.divider .line {
  height: 2px;
  width: 500px;
}

.divider .line:nth-of-type(1) {
  background: linear-gradient(
    270deg,
    #ee8cb1 -16.63%,
    rgba(238, 140, 177, 0) 87.08%
  );
}

.divider .line:nth-of-type(2) {
  background: linear-gradient(
    90deg,
    #ee8cb1 -16.63%,
    rgba(238, 140, 177, 0) 87.08%
  );
}

.footer-image {
  text-align: center;
}

.video-container {
  margin-top: 50px;
  margin-bottom: 200px;
  display: flex;
  justify-content: center;
  position: relative;
  aspect-ratio: 16 / 9;
}
