.hero-section {
  display: flex;
  align-items: center;
  justify-content: center;
  margin-bottom: 136px;
  min-height: 100vh;
}

.hero-section .main {
  display: flex;
  z-index: 1;
  position: relative;
}

.hero-section .main .image {
  z-index: 3;
  margin-right: 54px;
}

.hero-section .main .content {
  display: flex;
  flex-direction: column;
  justify-content: left;
  align-items: flex-start;
  z-index: 5;
}

.hero-section .main .content .logo {
  margin-bottom: 36px;
  cursor: pointer;
}

.hero-section .second-line {
  display: flex;
}

.hero-section .of-box {
  margin-top: 12px;
  margin-right: 8px;
}

.hero-section .horizontal-line {
  width: 101px;
  height: 6px;
  background-color: var(--grey);
}

.hero-section .horizontal-line:nth-of-type(1) {
  margin-bottom: 22px;
}

.hero-section .of {
  font-family: var(--primary-font);
  font-style: normal;
  font-weight: 400;
  font-size: 97px;
  line-height: 86px;
  text-align: center;
}

